import { useNavigate } from 'react-router-dom';
import {useState } from 'react';
import './styles/loginContent.css';
import Login from './login';


const LoginPage = ({ onLoginSuccess }) => {
  const [visible, setVisible] = useState(false);

  const navigate = useNavigate();
  
  const handleRegisterClick = () => {
    navigate('/register');
    
  };

  const handleLoginClick = () => {
    setVisible(true);
  };
  
  const handleCloseLogin = () => {
    setVisible(false);
  };

  const successLogin = (success) => {
    if (success) {
      navigate('/home');
    }
  }


  return (
    <div className="login-page">
      {/*  */}
      <header className="header">
        <div className="logo">Roleplay.AI</div>
        <div className="header-buttons">
          <button className="btn btn-primary" onClick={handleRegisterClick}>
            
          </button>
          <button className="btn btn-secondary" onClick={handleLoginClick}>
            
          </button>
          <Login visible={visible} onClose={handleCloseLogin} successLogin={successLogin} onLoginSuccess={onLoginSuccess} />
        </div>
      </header>

      {/*  */}
      <main className="login-content">
        {/*  */}
        <div className="animated-gifs">
          <div className="gif-item">
            <video 
              src={require('../imgs/harrypotter.mp4')}
              alt="AI1" 
              width="440" 
              height="100%"
              autoPlay
              loop
              muted
              preload="auto"
            />
          </div>
          <div className="gif-item">
            <video 
              src={require('../imgs/Einstein.mp4')}
              alt="AI2" 
              width="100%" 
              height="100%" 
              autoPlay
              loop
              muted
              preload="auto"
            />
          </div>
          <div className="gif-item">
            <video 
              src={require('../imgs/libai.mp4')}
              alt="AI3" 
              width="100%" 
              height="340px" 
              autoPlay
              loop
              muted
              preload="auto"
            />
          </div>
          <div className="gif-item">
            <div style={{width: '100%', height: '100%', backgroundColor: '#ff9800', display: 'flex', justifyContent: 'center', alignItems: 'center', color: 'white', fontSize: '16px', fontWeight: 'bold'}}>
              
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="login-container">
          <h1> Roleplay.AI</h1>
          <p>AI</p>
        </div>
      </main>
    </div>
  );
}

export default LoginPage;