import React, { useState, useRef, useEffect } from 'react';
import './styles/pullUpMenu.css';


const skillOptions = [
    { characterId: 1, skill: ["","", ""] },
    { characterId: 2, skill: ["","", ""] },
    { characterId: 3, skill: ["","", ""] },
    { characterId: 4, skill: ["","", ""] },
    { characterId: 5, skill: ["","", ""] },
    { characterId: 6, skill: ["","", ""] },
    { characterId: 7, skill: ["","", ""] },
    { characterId: 8, skill: ["","", ""] },
    { characterId: 9, skill: ["","", ""] },
  ];

const PullUpMenu = ({ characterId, onSelect }) => {
  // 
  const [showOptions, setShowOptions] = useState(false);
  // DOM
  const containerRef = useRef(null);
  const [options, setOptions] = useState([]);
  const [selectedSkill, setSelectedSkill] = useState("");



  // 
  const handleOptionClick = (option) => {
    if (option === ""){
        setSelectedSkill("");
    } else {
        setSelectedSkill(option);
    }
    onSelect(option);
      
    setShowOptions(false); // 
  };

  

    useEffect(() => {
        setSelectedSkill("");
        const options = skillOptions.find(item => item.characterId === characterId)?.skill || [];
        setOptions(options);
    }, [characterId]);
    
    // 
    useEffect(() => {
    
    const handleClickOutside = (event) => {
      if (containerRef.current && !containerRef.current.contains(event.target)) {
        setShowOptions(false);
      }
    };
    
    // 
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      // 
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);
  
  return (
    <div className="roleplay-skill-pull-up" ref={containerRef}>
      {/*  */}
      <p 
        className="roleplay-skill" 
        onClick={() => setShowOptions(!showOptions)}
      >
        {selectedSkill}
      </p>
      
      {/*  */}
      <div className={`pull-up-options ${showOptions ? 'show' : ''}`}>
        {options.map((option, index) => (
          <div 
            key={index}
            className="pull-up-option"
            onClick={() => handleOptionClick(option)}
          >
            {option}
          </div>
        ))}
      </div>
    </div>
  );
};

export default PullUpMenu;
