import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { RegisterPost } from './utils/api';
import { uploadImagePost } from './utils/api';

const RegisterPage = () => {
  // 
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    confirmPassword: ''
  });

  const navigate = useNavigate();
  
  // 
  const [avatar, setAvatar] = useState(''); // Base64
  const [avatarPreview, setAvatarPreview] = useState(''); // 
  const fileInputRef = useRef(null); // 
  
  // 
  const [errors, setErrors] = useState({});
  
  // 
  const [isRegistering, setIsRegistering] = useState(false);
  const [registerSuccess, setRegisterSuccess] = useState(false);

  // 
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    
    // 
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: null }));
    }
  };

  // 
  const handleAvatarUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      // 
      const fileType = file.type;
      const fileSize = file.size / 1024 / 1024; // MB
      
      if (!fileType.startsWith('image/')) {
        setErrors(prev => ({ ...prev, avatar: '' }));
        return;
      }
      
      if (fileSize > 5) {
        setErrors(prev => ({ ...prev, avatar: '5MB' }));
        return;
      }
      
      // 
      if (errors.avatar) {
        setErrors(prev => ({ ...prev, avatar: null }));
      }
      
      // 
      const reader = new FileReader();
      reader.onloadend = () => { 
        setAvatarPreview(reader.result); // 
        setAvatar(reader.result); // 
      };
      reader.readAsDataURL(file);

    }
  };

  // 
  const triggerFileUpload = () => {
    if (!isRegistering) {
      fileInputRef.current.click();
    }
  };

  // 
  const removeAvatar = () => {
    setAvatar('');
    setAvatarPreview('');
    fileInputRef.current.value = '';
    if (errors.avatar) {
      setErrors(prev => ({ ...prev, avatar: null }));
    }
  };

  // 
  const validateForm = () => {
    const newErrors = {};
    
    // 
    if (!formData.username.trim()) {
      newErrors.username = '';
    } else if (formData.username.length < 3) {
      newErrors.username = '3';
    } else if (formData.username.length > 20) {
      newErrors.username = '20';
    }
    
    // 
    if (!formData.password) {
      newErrors.password = '';
    } else if (formData.password.length < 6) {
      newErrors.password = '6';
    }
    
    // 
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = '';
    } else if (formData.confirmPassword !== formData.password) {
      newErrors.confirmPassword = '';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // 
  const handleRegister = async (e) => {
    e.preventDefault();
    
    if (validateForm()) {
      setIsRegistering(true);
  

      const imagePostUrl = `${process.env.REACT_APP_API_BASE_URL}/api/files/upload`;


      const imageResponse = await uploadImagePost(imagePostUrl, avatar);
      if (imageResponse.code !== 0) {
        setRegisterSuccess(false);
        setIsRegistering(false);
        setErrors({
          ...errors,
          avatar: imageResponse.msg || ''
        });
        return;
      }
      
      const registerUrl = `${process.env.REACT_APP_API_BASE_URL}/api/auth/register`;
      const response = await RegisterPost(registerUrl, {
        username: formData.username,
        password: formData.password,
        avatarUrl: imageResponse.data.fileUrl,
      });
      if (response.code === 0) {
        setRegisterSuccess(true);
        setIsRegistering(false);
        localStorage.setItem("login-success-user", JSON.stringify(
          {
              "username": response.data.username,
              "avatarUrl": response.data.avatarUrl,
              "userId": response.data.userId,
              "password": formData.password,
              "accessToken": response.data.accessToken,
          }));
        navigate('/home');
      } else {
        setRegisterSuccess(false);
        setIsRegistering(false);
        setErrors({
          ...errors,
          username: response.msg || ''
        });
      }
      
    }
  };

  return (
    <div style={styles.container}>
      {/*  */}
      <div style={styles.card}>
        <h1 style={styles.title}></h1>
        <p style={styles.subtitle}></p>
        
        {/*  */}
        <div style={styles.avatarContainer}>
          {/*  */}
          <div 
            style={styles.avatarPreview}
            onClick={triggerFileUpload}
            className="avatar-preview"
            title={avatarPreview ? '' : ''}
          >
            {avatarPreview ? (
              <img 
                src={avatarPreview} 
                alt="" 
                style={styles.avatarImg}
              />
            ) : (
              <div style={styles.avatarPlaceholder}>
                <span style={styles.avatarIcon}></span>
              </div>
            )}
            
            {/*  */}
            {avatarPreview && (
              <button 
                style={styles.removeAvatarBtn}
                onClick={(e) => {
                  e.stopPropagation(); // 
                  removeAvatar();
                }}
                title=""
                disabled={isRegistering}
              >
                
              </button>
            )}
          </div>
          
          {/*  */}
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleAvatarUpload}
            accept="image/*"
            style={styles.fileInput}
            disabled={isRegistering}
          />
          
          {/*  */}
          {errors.avatar && (
            <p style={styles.errorText}>{errors.avatar}</p>
          )}
          
          {/*  */}
          <p style={styles.avatarHint}>
            {avatarPreview ? '' : ''}
          </p>
        </div>
        
        <form onSubmit={handleRegister} style={styles.form}>
          {/*  */}
          <div style={styles.formGroup}>
            <label htmlFor="username" style={styles.label}>
               <span style={styles.required}>*</span>
            </label>
            <input
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleInputChange}
              placeholder=""
              style={{...styles.input, ...(errors.username ? styles.errorInput : {})}}
              disabled={isRegistering}
            />
            {errors.username && (
              <p style={styles.errorText}>{errors.username}</p>
            )}
          </div>
          
          {/*  */}
          <div style={styles.formGroup}>
            <label htmlFor="password" style={styles.label}>
               <span style={styles.required}>*</span>
            </label>
            <input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleInputChange}
              placeholder=""
              style={{...styles.input, ...(errors.password ? styles.errorInput : {})}}
              disabled={isRegistering}
            />
            {errors.password && (
              <p style={styles.errorText}>{errors.password}</p>
            )}
          </div>
          
          {/*  */}
          <div style={styles.formGroup}>
            <label htmlFor="confirmPassword" style={styles.label}>
               <span style={styles.required}>*</span>
            </label>
            <input
              type="password"
              id="confirmPassword"
              name="confirmPassword"
              value={formData.confirmPassword}
              onChange={handleInputChange}
              placeholder=""
              style={{...styles.input, ...(errors.confirmPassword ? styles.errorInput : {})}}
              disabled={isRegistering}
            />
            {errors.confirmPassword && (
              <p style={styles.errorText}>{errors.confirmPassword}</p>
            )}
          </div>
          
          {/*  */}
          <button
            type="submit"
            disabled={isRegistering}
            style={{...styles.button, ...(isRegistering ? styles.disabledButton : {})}}
          >
            {isRegistering ? (
              <>
                <svg style={styles.spinner} viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <circle className="spinner-circle" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" strokeLinecap="round" strokeDasharray="50" strokeDashoffset="0"></circle>
                </svg>
                ...
              </>
            ) : ''}
          </button>
        </form>
      </div>
      
      {/*  */}
      {registerSuccess && (
        <div style={styles.successToast}>
          <span></span>
        </div>
      )}
    </div>
  );
};

// 
const styles = {
  container: {
    minHeight: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
    padding: '20px',
    fontFamily: 'system-ui, -apple-system, sans-serif'
  },
  card: {
    background: 'white',
    borderRadius: '12px',
    boxShadow: '0 10px 30px rgba(0, 0, 0, 0.1)',
    width: '100%',
    maxWidth: '400px',
    padding: '40px 30px',
    boxSizing: 'border-box'
  },
  title: {
    fontSize: '24px',
    fontWeight: '700',
    color: '#333',
    margin: '0 0 8px 0',
    textAlign: 'center'
  },
  subtitle: {
    fontSize: '14px',
    color: '#666',
    margin: '0 0 20px 0',
    textAlign: 'center'
  },
  // 
  avatarContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    marginBottom: '30px'
  },
  avatarPreview: {
    width: '100px',
    height: '100px',
    borderRadius: '50%',
    border: '2px dashed #ddd',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    cursor: 'pointer',
    position: 'relative',
    overflow: 'hidden',
    transition: 'all 0.3s ease'
  },
  avatarImg: {
    width: '100%',
    height: '100%',
    objectFit: 'cover'
  },
  avatarPlaceholder: {
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  avatarIcon: {
    fontSize: '32px',
    color: '#999'
  },
  removeAvatarBtn: {
    position: 'absolute',
    right: '5px',
    bottom: '5px',
    width: '24px',
    height: '24px',
    borderRadius: '50%',
    background: 'rgba(0, 0, 0, 0.6)',
    color: 'white',
    border: 'none',
    fontSize: '16px',
    cursor: 'pointer',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: '0',
    transition: 'background 0.2s ease'
  },
  fileInput: {
    display: 'none'
  },
  avatarHint: {
    fontSize: '12px',
    color: '#666',
    marginTop: '8px',
    textAlign: 'center'
  },
  // 
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: '15px'
  },
  formGroup: {
    marginBottom: '10px'
  },
  label: {
    display: 'block',
    fontSize: '14px',
    color: '#333',
    marginBottom: '8px',
    fontWeight: '500'
  },
  required: {
    color: '#e74c3c',
    fontSize: '14px'
  },
  input: {
    width: '100%',
    padding: '12px 15px',
    borderRadius: '8px',
    border: '1px solid #ddd',
    fontSize: '16px',
    boxSizing: 'border-box',
    transition: 'all 0.3s ease'
  },
  inputFocus: {
    borderColor: '#4a90e2',
    outline: 'none',
    boxShadow: '0 0 0 3px rgba(74, 144, 226, 0.2)'
  },
  errorInput: {
    borderColor: '#e74c3c',
    boxShadow: '0 0 0 3px rgba(231, 76, 60, 0.1)'
  },
  errorText: {
    color: '#e74c3c',
    fontSize: '12px',
    marginTop: '5px',
    display: 'flex',
    alignItems: 'center',
    gap: '4px',
    marginBottom: '0'
  },
  // 
  button: {
    width: '100%',
    padding: '14px',
    borderRadius: '8px',
    border: 'none',
    backgroundColor: '#4a90e2',
    color: 'white',
    fontSize: '16px',
    fontWeight: '600',
    cursor: 'pointer',
    transition: 'all 0.3s ease',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    gap: '8px'
  },
  buttonHover: {
    backgroundColor: '#3a7bc8',
    transform: 'translateY(-2px)',
    boxShadow: '0 4px 12px rgba(74, 144, 226, 0.3)'
  },
  disabledButton: {
    backgroundColor: '#a0c4f5',
    cursor: 'not-allowed',
    transform: 'none',
    boxShadow: 'none'
  },
  // 
  spinner: {
    width: '18px',
    height: '18px',
    animation: 'spin 1s linear infinite'
  },
  // 
  successToast: {
    position: 'fixed',
    top: '20px',
    left: '50%',
    transform: 'translateX(-50%)',
    backgroundColor: '#2ecc71',
    color: 'white',
    padding: '12px 24px',
    borderRadius: '8px',
    boxShadow: '0 4px 12px rgba(46, 204, 113, 0.3)',
    fontSize: '14px',
    fontWeight: '500',
    animation: 'slideDown 0.3s ease-out forwards'
  }
};

// 
const style = document.createElement('style');
style.textContent = `
  .spinner-circle {
    animation: spinner-dash 1.5s ease-in-out infinite;
    stroke-dasharray: 80px, 200px;
    stroke-dashoffset: 0;
  }
  
  @keyframes spinner-dash {
    0% {
      stroke-dasharray: 1px, 200px;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 100px, 200px;
      stroke-dashoffset: -15px;
    }
    100% {
      stroke-dasharray: 100px, 200px;
      stroke-dashoffset: -125px;
    }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  @keyframes slideDown {
    0% { transform: translate(-50%, -100%); }
    100% { transform: translate(-50%, 0); }
  }
  
  input:focus {
    border-color: #4a90e2;
    outline: none;
    box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.2);
  }
  
  button:not(:disabled):hover {
    background-color: #3a7bc8;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
  }
  
  .avatar-preview:hover {
    border-color: #4a90e2;
    background-color: rgba(74, 144, 226, 0.05);
  }
  
  .removeAvatarBtn:hover {
    background-color: rgba(231, 76, 60, 0.8);
  }
`;
document.head.appendChild(style);

export default RegisterPage;
